@import "master";

.emojione {
  /* Emoji Sizing */
  font-size: inherit;
  height: 3ex;
  width: 3.1ex;
  min-height: 20px;
  min-width: 20px;
  /* Inline alignment adjust the margins  */
  display: inline-block;
  margin: -.2ex .15em .2ex;
  line-height: normal;
  vertical-align: middle;
}

img.emojione {
  /* prevent img stretch */
  width: auto;
}

/* AutoComplete styles for Emoji One */

.dropdown-menu {
  list-style: none;
  padding: .3em 0 0;
  margin: 0;
  border: 1px solid $primary-color;
  background-color: $white-color;
  border-radius: 2px;
  overflow: hidden;
  font-size: 14px;
  letter-spacing: .025em;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, .2);
}

.dropdown-menu a:hover {
  cursor: pointer;
}

.dropdown-menu li {
  letter-spacing: 0;
  display: block;
  float: none;
  margin: 0;
  padding: 0;
  border: none;
}

.dropdown-menu li:before {
  display: none;
}

.dropdown-menu .textcomplete-footer {
  margin-top: .3em;
  background: #e6e6e6;
}

.dropdown-menu .textcomplete-footer a {
  color: $disabled-color;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: .05em;
  line-height: 2.1818em;
  padding-left: 1.8181em;
  font-size: .84em;
}

.dropdown-menu .textcomplete-footer .arrow {
  margin-left: .8em;
  font-size: 1.3em;
}

.dropdown-menu li {

  .emojione {
    vertical-align: middle;
    font-size: 1.23em;
    width: 1em;
    height: 1em;
    top: -1px;
    margin: 0 .3em 0 0;
  }
  a {
    display: block;
    height: 100%;
    line-height: 1.8em;
    padding: 0 1.54em 0 .615em;
    color: #4f4f4f;
  }
}

.dropdown-menu .active, .dropdown-menu li:hover {
  background: #6E6E6E;
  color: $white-color;
}

.dropdown-menu .active a, .dropdown-menu li:hover a {
  color: inherit;
}