@import "master";


/*This is necessary for error messages to position correctly*/

body {
  margin-top: 15px;
}

/*Advertisement Banner*/

.adBanner {
  background-color: $error-color;
  width: 100%;
  height: 8em;
  border-radius: 2px;
}

/*This is for the overtyping text*/

.overlimit {
  color: $error-color;
}

.overlimitText {
  color: $error-color;
}

.overlimitText:focus {
  border-color: $error-color;
}

/*This is for the comments divider*/

.specialHr {
  width: 75%;
}

/*This fixes the comment and status box buttons*/

.imageButton {
  margin-left: 15px;
}

.socialButton {
  margin-right: 15px;
}

/*Size for the comment images*/

.commentImages {
  width: 75%;
  height: 300px;
}

/*Paginator style rules*/

.hidden-paginator {
  display: none;
}

/*Buttons*/

.page-bottom {
  margin-bottom: 15px;
}

.dropdown-caret {
  background-color: purple;
  border-color: purple;
}